@import "../../_variables";

.login-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 11px;
  &__input {
    width: 100%;
  }
  &__error {
    margin-top: 5px;
    color: $color-error;
  }
  @media (min-width: $mw4) {
    gap: 18px;
  }
}